				font-size: 16px;
				height: 40px;
			}
	
	.add-update-preview .el-date-editor /deep/ .el-input__inner {
				border: 1px solid #E8E8E8;
				border-radius: 5px;
				padding: 0 10px 0 30px;
				color: #000;
				background: #fff;
				width: 100%;
				font-size: 16px;
